.context-theme {
    grid-column: 1/3;
    padding: 20px;
    margin: 10px;
    border-radius: 25px;
    transition: 0.5s all ease-out;

    &.light {
        color: black;
        background-color: white;
    }

    &.dark {
        color: white;
        background-color: black;
    }
}